import * as asyncstorage from '../components/asyncstorage';
var Database_Name = 'MobileService';  
var Version = 1.0;  
var Text_Description = 'Mobile Service database';  
var Database_Size = 2 * 1024 * 1024;  

const db = async(databaseName) => {
    const my_db = (databaseName === '') ? await asyncstorage.getItem("database") :  databaseName;
    return openDatabase(my_db, Version, Text_Description, Database_Size); 
}

export {
    db
}
