export const verifyIP = (IP_value) => {
    var errorString = "",
                theName = "IP address",
                ipAddress, ipp,
                ipPattern = /^([a-zA-Z0-9]|[a-zA-Z0-9][a-zA-Z0-9\-]{0,61}[a-zA-Z0-9])(\.([a-zA-Z0-9]|[a-zA-Z0-9][a-zA-Z0-9\-]{0,61}[a-zA-Z0-9]))*$/,
                ipArray,
                thisSegment
                            /*,
                             extensionLength*/
            ;


            if (IP_value.indexOf(":") != -1) {
                ipp = IP_value.split(":");
                ipAddress = ipp[0];
            } else {
                ipAddress = IP_value;
            }

            ipArray = ipAddress.match(ipPattern);


            if (ipAddress == "0.0.0.0")
                errorString = errorString + theName + ': ' + ipAddress + ' is a special IP address and cannot be used here.';
            else if (ipAddress == "255.255.255.255")
                errorString = errorString + theName + ': ' + ipAddress + ' is a special IP address and cannot be used here.';
            if (ipArray == null)
                errorString = errorString + theName + ': ' + ipAddress + ' is not a valid IP address.';
            else {
                for (var i = 0; i < 4; i++) {
                    thisSegment = ipArray[i];
                    if (thisSegment > 255) {
                        errorString = errorString + theName + ': ' + ipAddress + ' is not a valid IP address.';
                        i = 4;
                    }
                    if ((i == 0) && (thisSegment > 255)) {
                        errorString = errorString + theName + ': ' + ipAddress + ' is a special IP address and cannot be used here.';
                        i = 4;
                    }
                }
            }
            /*extensionLength = 3;*/
            return errorString;
}

