 const todayDay = () => {
    var d = new Date();
    var weekday = new Array(7);
    weekday[0] = "Sunday";
    weekday[1] = "Monday";
    weekday[2] = "Tuesday";
    weekday[3] = "Wednesday";
    weekday[4] = "Thursday";
    weekday[5] = "Friday";
    weekday[6] = "Saturday";
    return weekday[d.getDay()];
}

 const getWeekNew = ()=> {
    var datetimeFilter = getCurrentDateTime('YYYY-MM-DD');
    //var day = getPayrollDay();
    var day = 'Sunday';
    if (day == 0) {
        day = 'Sunday';
    }

    if (day == todayDay()) {
        var day1 = 'Next ' + day;
        var datetimeFilter1 = Date.parse(day1).toString('yyyy-MM-dd');
    }
    else {
        var day1 = 'Next ' + day;
        day = 'Last ' + day;
        datetimeFilter = Date.parse(day).toString('yyyy-MM-dd');
        var datetimeFilter1 = Date.parse(day1).toString('yyyy-MM-dd');
    }

    var weekArray = new Array();
    weekArray[0] = datetimeFilter;
    weekArray[1] = datetimeFilter1;

    return weekArray;
}

